<template>
  <div id="detail">
     <div>
         <p><img :src="detail.images.small" alt="" width="100%" height="300"></p>
        <h3>片名：{{detail.original_title}}</h3>
         <h3>导演：{{detail.directors[0].name}}</h3>
        <h3>主演：{{reversedMessage}}</h3>
        <h3>类型:{{moveType}} </h3>
        <h3>年份：{{detail.year}} </h3>
         <h3>上映地区：{{detail.countries[0]}}</h3>
         <h3>影片简介：{{detail.summary}}</h3>
         <yd-button type="primary" size="large" @click.native="goBack">返回</yd-button>
     </div>
  </div>
</template>

<script>
    export default {
        data(){
            return{
                detail:[]
            }
        },
        mounted(){
            var _this = this;
            const id = this.$route.params.id;
            this.$axios.get('/api/movie/subject/'+ id).then(function(res){
                _this.detail = res.data
            }).catch(function(err){
                console.log(err);
            })
        },
        computed:{
            reversedMessage(){
                let str = [];
                for(var i = 0;i < this.detail.casts.length;i++){
                    str.push(this.detail.casts[i].name)
                }
                return str.join(' ')
            },
            moveType(){
                let str = [];
                for(var i = 0;i < this.detail.genres.length;i++){
                    str.push(this.detail.genres[i])
                }
                return str.join(' ')
            }
        },
        methods:{
            goBack(){
                history.back()
            }
        }
    }
</script>

<style>
    #car h3{
        margin-bottom: 10px;
        font-size: 12px;
    }
</style>


